import React, { Component } from 'react'
import {Menu,Icon} from 'antd'
import {Link} from 'dva/router'
import { connect } from 'dva';
const {SubMenu} = Menu
let mapState = (state)=>{
  return {...state.main,...state.global}
}
let mapDispatch = dispatch=>{
  return {
    setOpenKeys(openKeys){
      dispatch({type:'main/setOpenKeys',openKeys});
    }
  }
}
@connect(mapState,mapDispatch)
class Aside extends Component {
  constructor(props){
    super(props);
    this.state = {
      groups:[]
    }
  }
  static getDerivedStateFromProps(nextProps){
    let {childRoutes} = nextProps;
    let {viewAuthority} = nextProps;
    if(viewAuthority.length === 0){
      return {}
    }
    // 对路由信息进行分类管理
    let initGroups = [];
    childRoutes && childRoutes.forEach(item=>{
      let result = initGroups.find(val=>val.groupName === item.groupName);
      if(result){
        result.items.push(item)
      }else{
        initGroups.push({groupName:item.groupName,groupIcon:item.groupIcon,items:[item],id:item.id});
      }
    });
    function isAutho(arr){
      arr.forEach((item,key)=>{
        if(viewAuthority.findIndex(val=>val.view_id === item.id) === -1){
          item.noAutho = true;
        }
        if(item.items){
          isAutho(item.items);
        }
      })
    }
    isAutho(initGroups);
    initGroups.forEach(item=>{
      item.items = item.items.filter(val=>val.showAside !== 'no');
      if(item.items.every(val=>val.noAutho)){
        item.noAutho = true;
      }else{
        item.noAutho = false;
      }
    })
    return {
     groups:initGroups
    }
  }
  render() {
    window.aab = this;
    let {groups} = this.state;
    let {openKeys,setOpenKeys} = this.props;
    return <Menu
      mode="inline"
      style={{ height: '100%', borderRight: 0 }}
      openKeys={openKeys}
      theme="dark"
      onOpenChange={(openKeys)=>setOpenKeys(openKeys)}
    >
      {
        groups && groups.map(item=>{
          if(item.noAutho){
            return null;
          }
          return <SubMenu key={item.id} title={<span><Icon type={item.groupIcon} />{item.groupName}</span>}>
            {
              item.items && item.items.map(val=>{
                if(val.noAutho){
                  return null;
                }
                return <Menu.Item key={val.id}>
                  <Link to={{pathname:val.path,state:{parent:item.groupName,child:val.text,id:val.id}}}>{val.text}</Link>
                </Menu.Item>
              })
            }
          </SubMenu>
        })
      }
    </Menu>
  }
}
export default Aside;