import React, { Component } from 'react'
import {Avatar, Menu, Dropdown} from 'antd'
import {connect} from 'dva'
import {withRouter} from 'dva/router'
import styles from './Header.module.css'

let mapState = store => {
    window.store = store;
    return {...store.login,...store.main}
}
@withRouter
@connect(mapState)
class Header extends Component {
  componentDidMount(){
    this.props.dispatch({type:'main/getUserInfo'});
  }
  render() {
    const {user_name} = this.props.userInfo;
    const menu = (
      <Menu style={{width: '120px'}}>
        <Menu.Item>
          <a target="_blank" rel="noopener noreferrer" href="http://www.alipay.com/">个人中心</a>
        </Menu.Item>
        <Menu.Item>
          <a target="_blank" rel="noopener noreferrer" href="http://www.taobao.com/">我的班级</a>
        </Menu.Item>
        <Menu.Divider />
        <Menu.Item>
          <a target="_blank" rel="noopener noreferrer" href="http://www.taobao.com/">设置</a>
        </Menu.Item>
        <Menu.Item key="logout" onClick={()=>this.props.dispatch({type:'login/logout'})}>
          退出登录
        </Menu.Item>
      </Menu>
    )
    return  (
      <div>
        <Dropdown overlay={menu} placement="bottomRight">
          <span className={styles.user}>
            <Avatar src="https://cdn.nlark.com/yuque/0/2019/png/anonymous/1547609339813-e4e49227-157c-452d-be7e-408ca8654ffe.png?x-oss-process=image/resize,m_fill,w_48,h_48/format,png" />
            {user_name}
          </span>
        </Dropdown>
      </div>
    )
  }
}
export default Header;
