import React, { Component } from 'react'
import BasicLayout from '../../components/BasicLayout'
import RouterView from '../../utils/router-view'
import Aside from '../../components/aside'
import BreadCrumb from '../../components/breadcrumb'
import Header from '../../components/Header'
import {connect} from 'dva'
import NoAutho from '../../components/NoAutho'
let mapState = store => {
  return {...store.global}
}
@connect(mapState)
class Authorization extends Component{
  render(){
    let {history,viewAuthority} = this.props;
    if(!history.location.state || viewAuthority.length === 0){
      return <NoAutho />;
    }
    let index = viewAuthority.findIndex(item=>item.view_id === history.location.state.id);
    if(index=== -1){
      return <NoAutho />;
    }
    return <RouterView childRoutes={this.props.childRoutes} />
  }
}
class Main extends Component {
  render() {
    window.main = this;
    return <BasicLayout 
        breadcrumb={<BreadCrumb />}
        aside={<Aside childRoutes={this.props.childRoutes} />}
        header={<Header />}
      >
        <Authorization {...this.props} />
      </BasicLayout>
  }   
}

export default Main